import React, { useState, useEffect } from 'react';
import querystring from "querystring"

function Confirm() {

    const keys = [
        {key: "fn", value: "First Name", show: (data)=> true},
        {key: "ln", value: "Last Name", show: (data)=> true},
        {key: "dob", value: "DOB", show: (data)=> true},
        {key: "sdo", value: "Single Dose?", show: (data)=> true},
        {key: "lnum", value: "1st Lot#", show: (data)=> true},
        {key: "fddate", value: "1st Dose", show: (data)=> true},
        {key: "sdlnum", value: "2nd Lot#", show: (data)=> !data["sdo"] && !data["slnm"]},
        {key: "sddate", value: "2nd Dose", show: (data)=> !data["sdo"]}
    ]

    const [data, setData] = useState({});

    const [immune, setImmune] = useState(false);

    useEffect(()=> {
        var dataB64 = querystring.parse(window.location.search.split('?')[1])["data"];
        var dataObj = JSON.parse(atob(dataB64));
        setData({...data, ...dataObj})
        
        var isImmune = (dataObj["lnum"] && dataObj["fddate"] ) 
                                        && (
                                            (dataObj["sdo"])
                                            ||
                                            (!dataObj["sdo"] && dataObj["sddate"] 
                                                && (dataObj["slnm"] 
                                                    || 
                                                    (!dataObj["slnm"] &&  dataObj["sdlnum"]) )));
        setImmune(isImmune);
    }, [])

    return (
        <React.Fragment>
            <h1 className={immune ? "immune" : "not-immune"}>{immune ? "IMMUNE" : "NOT IMMUNE"}</h1>
            <div className="data view">
                {
                keys.filter((key, indx) => {
                    return keys[indx].show(data);
                })
                .map((key) => {
                    return <React.Fragment>
                        <label>{key.value}:</label>
                        <div>{data[key.key] != undefined ? String(data[key.key]) : "N/A"}</div>
                    </React.Fragment>
                })
                }
            </div>
        </React.Fragment>
    );

}

export default Confirm;